/**
*
* Programmation générique :  Base de toutes les pages => gloables aux projets
*
* */

* {
	margin:0;
	padding:0;
	-webkit-box-sizing: border-box;
	box-sizing: border-box; 
}
html {
	width: 100%;
	height: 100%;
	font-size: 1.1rem; // 1rem generalement
	line-height: 1.5rem;
	scroll-behavior: smooth;
	// font-size: 62.5%;
	body {
		background-color: $body-bg !important;
		color: $white !important;
		font-family: $font-body;
		font-size: 1.2rem;
		font-display: swap;
		margin: 0;
		padding: 0 !important;
		height: 100vh;
		overflow-x: hidden;
		cursor: none; // Retire le curseur par defaut
		// Puces
		position: relative; // Si footer en absolute
		display : table;
		width: 100%;
		ul,
		li {
			list-style-type: none;
		}
		// Liens
		a {
			color: white;
			text-decoration: none;
			-webkit-text-decoration-line: none;
			text-decoration-line: none;
			&:link {
				cursor: none; // Retire le curseur
			}
			&:hover {
				-webkit-transition: all 0.5s ease-in-out;
				-o-transition: all 0.5s ease-in-out;
				transition: all 0.5s ease-in-out;
			}
		}
		// Redimensionnement d une image en conservant le ratio lors du responsive
		img {
			max-width: 100%;
			height: auto;
			-o-object-fit: contain;
			object-fit: contain;
		}
	}
}

$underlineColor: #00B388;
$underlineHeight: 5%;
.link_hover {
	font-style: italic;
	font-weight: bold;
	position: relative;
	text-decoration: none;
	display: inline-block;
	padding: 0 1px;
	transition: color ease 0.3s;
	
	&::before, &::after {
		content: '';
		position: absolute;
		background-color: $white;
		z-index: -1;
		height: $underlineHeight;
	}
	
	&::before {
		width: 0%;
		left: 0;
		bottom: 0;
		transition: width ease 0.4s;
	}
	
	&::after {
		width: 100%;
		left: 0;
		bottom: 0;
		transition: all ease 0.6s;
	}
	
	&:hover {

		&::before {
		width: 100%;
		}
		
		&::after {
		left: 100%;
		width: 0%;
		transition: all ease 0.2s;
		}
	}
}
