/**
*
* Barre de navigation et de son tremblement
*
* */

// ScrollBar
 body,
 html{
     scroll-behavior: smooth;
     scrollbar-width: none; // firefox
 }
 ::-webkit-scrollbar { // autres navigateur
     display: none;
 }

// Navbar
.navbar {
	position: fixed;
	animation: shake 20s;
	animation-iteration-count: infinite;
	z-index: 10;
	text-transform: lowercase;
	// border: blue solid 1px;
	ul {
		padding-left: 50px;
		position:relative;
		li {
			margin: 10vh 0 0 20px; //espacement liens
			transform: translateY(50%);
			a {
				text-decoration: none;
				position: relative;
				color: $white;
				font-size: 1.5rem;
				font-weight: 500;
				transition: color 500ms;
			}
			a:before{
				content:"";
				position:absolute;
				background: transparent;
				width: 1rem;
				height: 1rem;
				left: -42px;
				top: 50%;
				transform: translateY(-75%);
				border-radius: 50px;
				border: 4px solid $white;
				transition: background 500ms;
				background: $body-bg;
			}
			.active{
				font-weight: 700;
				color: $white;
			}
			.active::before {
				background: $white;
				left:-43px;
				border: 5px solid $white;
			}
		}
	}
	#bar_fix{
		content:"";
		position:absolute;
		width:1px;
		height:100vh;
		left: 39.5px;
		background: $white;
		z-index: -1;
		opacity: 50%;
	}
	#bar_anim {
		content: "";
		position: absolute;
		width: 2px;
		left: 39px;
		height: 19%;
		background: $white;
		z-index: -1;
	}
}

.navbar ul li a:hover {
	font-weight: 700;
}

